body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 100px 1fr;
  border: 1px solid grey;
  min-height: 600px;
  margin: 10px;
}

#header {
  grid-column: span 2;
  background-color: rgb(75, 214, 207);
  color: white;
  font-size: 40px;
  padding: 15px 10px;
}
#directory {
  border: 1px solid grey;
}

form {
  text-align: center;
  padding: 20px;
}
input {
  width: 300px;
  padding: 5px;
  margin: 5px;
}
button {
  width: 75px;
  background-color: rgb(75, 214, 207);
  color: white;
  padding: 5px;
}

#details {
  border: 1px solid grey;
}

h3 {
  width: 390px;
  border: 1px solid grey;
  margin: auto;
  padding: 5px;
  margin-bottom: 10px;
}
